<template>
  <div class="common-wrapper-outer">
    <div class="common-wrapper-inner ghibli-queryset">

      <GhibliQuery
                :image="image"
                :ask="question[question_index].ask"
                :ans1="question[question_index].ans1"
                :ans2="question[question_index].ans2"
                :total="totalCnt"
                @next="next">
      </GhibliQuery>

    </div>
  </div>
</template>

<script>
import score from './score'

export default {
  name: 'QuerySet',
  
  setup() {
    var image = "https://img.icons8.com/color/480/000000/totoro.png"

    // image = "assets/ghibli/ponyo.png"

    return { image }
  },

  methods: {
    next(ans) {
      if (this.question_index == this.question.length - 1) {
        this.answer += '/' + ans;
        var score_index = score(this.answer)
        console.log(score_index)
        this.$router.push({ path: "/ghibli/shizuku" })
      } else {
        this.question_index++
        this.answer += '/' + ans;
      }
    }
  },

  data: () => ({
    totalCnt: 6,
    answer: "",
    question_index: 0,
    question: [
      {ask: "질문입니당", ans1: "답변1", ans2: "답변2"},
      {ask: "질문입니당", ans1: "답변1", ans2: "답변2"},
      {ask: "질문입니당", ans1: "답변1", ans2: "답변2"},
      {ask: "질문입니당", ans1: "답변1", ans2: "답변2"},
      {ask: "질문입니당", ans1: "답변1", ans2: "답변2"},
      {ask: "질문입니당", ans1: "답변1", ans2: "답변2"}
    ],
  }),
}
</script>


<style>

.ghibli-queryset {
  background-color: #F5F9FC;
}


</style>