<template>

  <div class="common-wrapper-outer">
    <div class="common-wrapper-inner">

      <Particles
              id="tsparticles"
              :particlesInit="particlesInit"
              :particlesLoaded="particlesLoaded"
              :options="{
                  background: {
                      color: {
                          value: '#bfcdcc'
                      }
                  },
                  fpsLimit: 60,
                  interactivity: {
                      detectsOn: 'canvas',
                      events: {
                          onClick: {
                              enable: true,
                              mode: 'push'
                          },
                          onHover: {
                              enable: true,
                              mode: 'repulse'
                          },
                          resize: true
                      },
                      modes: {
                          bubble: {
                              distance: 400,
                              duration: 2,
                              opacity: 0.8,
                              size: 40
                          },
                          push: {
                              quantity: 4
                          },
                          repulse: {
                              distance: 80,
                              duration: 0.4
                          }
                      }
                  },
                  particles: {
                      color: {
                          value: '#ffffff'
                      },
                      rotate: {
                        value: 0,
                        random: true,
                        direction: 'clockwise',
                        animation: {
                          enable: true,
                          speed: 5,
                          sync: false
                        }
                      },
                      links: {
                          color: '#ffffff',
                          distance: 150,
                          enable: true,
                          opacity: 0,
                          width: 0
                      },
                      collisions: {
                          enable: true
                      },
                      move: {
                          direction: 'none',
                          enable: true,
                          outMode: 'bounce',
                          random: false,
                          speed: 3,
                          straight: false
                      },
                      number: {
                          density: {
                              enable: true,
                              value_area: 800
                          },
                          value: 80
                      },
                      opacity: {
                        random: {
                          minimumValue: 0.1,
                          enable: true
                        },
                        value: 1,
                        animation: {
                          count: 0,
                          enable: true,
                          speed: 1,
                          sync: false,
                          destroy: none,
                          minimumValue: 0.1,
                          startValue: random
                        }
                      },
                      shape: {
                          type: 'image',
                          options: {
                            image: [{
                              src: 'https://www.iconeasy.com/icon/png/System/Anna%20Sui%20inspired%20iconset%20PLUS/Makkuro%20Kurosuke.png'
                            },
                            {
                              //src: 'https://www.pngkey.com/png/full/188-1887025_i-made-a-cute-and-quick-transparent-calcifer.png'
                            }
                            ]
                          }
                      },
                      size: {
                        random: {
                          minimumValue: 5,
                          enable: true
                        },
                        value: 20,
                      }
                  },
                  detectRetina: true
              }"
      />

      <div id="foreground">

        <div class="th-wrapper">
          <div class="th-image">
            <img v-bind:src="require(`@/assets/${image}`)" alt="test image">    
          </div>
          <div class="th-name">
            <p>{{ title }}</p>
          </div>
          
        </div>

        <a className="bn3" @click="$router.push({ path: `${link}` })">시작하기</a>

        <p id="countText"> 참여한 사람 </p>
        <p id="count"> {{ count }} </p>

        <social-button :title="title"/>

      </div>

    </div>
  </div>

</template>

<script>
import countapi from 'countapi-js';
import { ref } from 'vue';
import SocialButton from './Button/SocialButton.vue';

export default {
  components: { SocialButton },
  name: 'Theme',
  props: {
    theme: String,
  },
  setup(props) {
    var image = "logo.png"
    var title = ""
    var link = ""
    var count = ref(0)

    if (props.theme == "ghibli") {
      image = "ghibli/totoro3.png"
      title = "나랑 비슷한 지브리 캐릭터는?"
      link = "/q/ghibli"
    }

    countapi.hit('meansoup.github.io', 'test').then((result) => {
        console.log(result.value);
        count.value = result.value;
    });

    return {
        image, title, link, count
    }
  },
  methods: {
  }
}
</script>

<style>
@import url(//fonts.googleapis.com/earlyaccess/nanumpenscript.css);



#tsparticles {
  width: 100%;
  height: 100%;
  position: relative;
}

#foreground {
  position: relative;
	width: 90%;
	margin: 0px auto;
  top: -100%;
}

.th-image img {
	width: 100%;
	vertical-align: middle;
}

.th-name {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 30px;
	padding: 5px 10px;
	background-color: white;
  opacity: 0.8;
	text-align: center;
	position: relative;
	top: 50%;
	left: 50%;
	transform: translate( -50%, -50% );
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
}



.bn3 {
  display: inline-block;
  padding: 0.5em 1.7em;
  margin: 0 0.1em 0.1em 0;
  border: 0.3em solid rgb(255, 255, 255);
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 300;

  /* background: 와 같게 */
  color: #bfcdcc;
  font-weight: 700;
  text-shadow: 0 0.04em 0.04em rgba(255, 255, 255, 0.253);
  text-align: center;
  transition: all 0.2s;
  background-color: rgb(255, 255, 255);
  width: 60%;
}

.bn3:hover {
  color: white;
  background-color: rgba(255, 255, 255, 0);
}

#countText {
  color: white;
  font-weight: 600;
  margin-top: 32px;
}


#count {
  color: white;
  font-weight: 700;
  font-size: 25px;
}


</style>