<template>

    <div class="ldBar no-label" id="mybar"
          style="width: 30vh; height: 30vh"
          data-aspect-ratio="none"
          data-type="fill"
          :data-img="image"
          data-value="0">
    </div>

      <p id="question-ask">
        <span id="question-ask-big">{</span>
        
        &nbsp;&nbsp;&nbsp;&nbsp;
        {{ ask }}
        &nbsp;&nbsp;&nbsp;&nbsp;

        <span id="question-ask-big">}</span>
      </p>

    <query-button class="questionOnlyAnsBtn" @click="select(1)" :content="ans1"/>
    <query-button class="questionOnlyAnsBtn" @click="select(2)" :content="ans2"/>

</template>

<script>
import QueryButton from './QueryButton.vue';
export default {
  components: { QueryButton },
  name: 'Query',
  props: {
    ask: String,
    ans1: String,
    ans2: String,
    image: String,
    total: Number
  },

  data: () => ({
    current: 0
  }),

  methods: {
    select(ans) {
      this.$emit("next", ans)
      this.current = this.current + 1

      var myBar = new window.ldBar("#mybar");
      myBar.set(this.current * 100 / this.total);
    }
  }
}
</script>

<style>
.questionOnlyAnsBtn {
  width: 90%;
  height: 20vh !important;
  margin-top: 1vh;
}

#question-ask {
  position: relative;
  font-size: 30px;
  font-family: BinggraeSamancoBold;
	text-align: center;
  display: inline;
}

#question-ask-big {
  font-size: 60px;
}

/* for progress bar */
.ldBar.no-label {
  margin: 0 auto;
}
</style>