<template>
  <button class="query-button"
    v-on:mouseover="mouseover"
    v-on:mouseleave="mouseleave"> {{content}} </button>
</template>

<script>
export default {
  name: 'QueryButton',
  props: {
    content: String
  }
}
</script>

<style>
:root {
  --bg-color: #45535E;
  --hover-color: #F5F9FC;
}

.query-button {
  background: var(--bg-color);
  color: var(--hover-color);
  height: 100%;
  width: 100%;
  border:none;
  position:relative;
  font-size:1.6em;
  padding:0 2em;
  cursor:pointer;
  transition:800ms ease all;
  outline:none;
  border-radius: 30px;
}
.query-button:hover {
  background:var(--hover-color);
  color:var(--bg-color);
}

</style>