import { createApp } from 'vue'
import vuetify from './plugins/vuetify'
import Particles from 'particles.vue3'
import App from './App.vue'
import { createRouter, createWebHashHistory } from 'vue-router'
import HelloWorld from './components/HelloWorld'

import GhibliQuerySet from './components/Theme/Ghibli/QuerySet'
import GhibliQuery from './components/Theme/Ghibli/Query'
import GhibliAnswer from './components/Theme/Ghibli/Answer'

import LolQuery from './components/Lol/Query'
import LolAnswer from './components/Lol/Answer'

import question from './components/Question'
import Theme from './components/Theme'


import socialButton from './components/Button/SocialButton'

const routes = [
    { path: '/hello', component: HelloWorld },
// querys
    { path: '/q/ghibli', component: GhibliQuerySet},
    { path: '/lol', component: LolQuery},

// answers
    { path: '/ghibli/:id', component: GhibliAnswer, props: true },
    { path: '/lol/:id', component: LolAnswer, props: true },

// theme
    { path: '/:theme', component: Theme, props: true }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
  })

const app = createApp(App)
app.use(router);
app.use(vuetify)
app.use(Particles)

app.component('Question', question)
app.component('GhibliQuery', GhibliQuery)
app.component('SocialButton', socialButton)

app.mount('#app')
