<template>
  <div class="common-wrapper-outer common-overflow">
    <div class="common-wrapper-inner common-overflow-inner" v-bind:style="{backgroundColor: color}">
      <p >{{ name }}</p>

      <div id="ghibli-answer-image">
        <img v-bind:src="require(`@/assets/${image}`)" alt="test image">
      </div>
      <q>
        <p id="speak">
          &nbsp;&nbsp;&nbsp;&nbsp;
          {{   bigDesc   }}
          &nbsp;&nbsp;&nbsp;&nbsp;
        </p>
      </q>
      <p>{{ smallDesc }}</p>
      
          <p id="ghibli-answer-desc"><img width="20" src="https://cdn.icon-icons.com/icons2/1465/PNG/512/536bread_100562.png"> 테스트 작성입니다 </p>
          <p id="ghibli-answer-desc"><img width="20" src="https://cdn.icon-icons.com/icons2/1465/PNG/512/536bread_100562.png"> 테스트 작성입니다 </p>
          <p id="ghibli-answer-desc"><img width="20" src="https://cdn.icon-icons.com/icons2/1465/PNG/512/536bread_100562.png"> 테스트 작성입니다 </p>
          <p id="ghibli-answer-desc"><img width="20" src="https://cdn.icon-icons.com/icons2/1465/PNG/512/536bread_100562.png"> 테스트 작성입니다 </p>
          <!-- https://icon-icons.com/icon/bread/100562 -->

      <social-button :title="title"/>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Answer',
  data: () => ({
    title: "나랑 비슷한 지브리 캐릭터는?"
  }),
  props: {
    id: String,
  },
  setup(props) {
    var name = ""
    var image = ""
    var bigDesc = ""
    var smallDesc =""
    var color ="#ffffff"

    if (props.id == "shizuku") {
      name = "test"
      image = "ghibli/shizuku.jpg"
      bigDesc = "그래 나도 하면 되는거야"
      smallDesc = "ttest"
      color = "#d29693"
    } else if (props.id == "mei") {
      image = "ghibli/mei.jpg"
      color ="#e2d39c" // 노랑색
    } else if (props.id == "totoro") {
      image = "ghibli/totoro.jpg"
      color ="#ced9cb"
    } else if (props.id == "kiki") {
      image = "ghibli/kiki.jpg"
      color ="#aebdc2"
    } else if (props.id == "tombo") {
      image = "ghibli/tombo.jpg"
      color ="#aea09d" // 하늘색
    }



    return {
        name, image, bigDesc, smallDesc, color
    }
  }
}
</script>

<style>

ui-hide {
  display: none;
}

#ghibli-answer-image img {
	width: 100%;
  vertical-align: middle;
}

#speak {
  position: relative;
  font-size: 30px;
  font-family: BinggraeSamancoBold;
	text-align: center;
  top: -40px;
  display: inline;
}

q {
  font-weight: 700;
  font-size: 150px;
	text-align: center;

  font-family: BinggraeSamancoBold;
}

#ghibli-answer-desc {
  font-family: BinggraeSamanco;
}
</style>